            // this.$message({message: 'Ê¨¢ËøéÊ∏∏ÂÆ¢' + this.visitor + 'ËÆøÈóÆ', center: 'true', type: "success",})
            // this.$notify.info({
            //   title:'ÊàêÂäü',
            //   message:'Ê¨¢ËøéÊ∏∏ÂÆ¢' + this.visitor + 'ËÆøÈóÆ',
